import SingleItem from '../components/SingleItem';
import gql from 'graphql-tag';
import { Query } from 'react-apollo';

const SINGLE_ITEM_QUERY = gql`
  query SINGLE_ITEM_QUERY($id: ID!) {
    item(where: { id: $id }) {
      id
      title
      description
      largeImage
    }
  }
`;

const item = props => {
  return <SingleItem id={props.query.id} />;
};

export default item;
